import axios from "axios";

let state = {
    tariffs: [],
    currentTariff: {}
}

let mutations = {
    setTariffs(state, array) {
        state.tariffs = array
    },
    setCurrentTariff(state, object) {
        state.currentTariff = object
    }
}

let actions = {
    async getTariffs(ctx) {
        await axios({
            url: '/tariffs',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setTariffs', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getTariffInfo(ctx, id) {
        await axios({
            url: `/tariff/${id}`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setCurrentTariff', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    }
}

export default {
    state,
    mutations,
    actions
}
